import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue';
import uniIcons from "@/components/uni-icons/uni-icons.vue";
import uniPopup from "@/components/uni-popup/uni-popup.vue";
import uniRate from '@/components/uni-rate/uni-rate.vue';
import uniCard from '@/components/uni-card/uni-card.vue';
export default {
	components: { uniNavBar,uniIcons,uniRate,uniCard,uniPopup },
	data(){
		return{
			bookName:'',
			hasBookShelf:true
		}
	},
	watch:{
		hasBookShelf(val){
			this.val=false
		}
	},
	methods:{
		back(){
			uni.navigateTo();
		},
		/* 书本保存到本地 */
		saveBookLocal(savedFilePath, bookName) {
			/* 读取临时文件 */
			console.log(savedFilePath)
			plus.io.resolveLocalFileSystemURL(
				savedFilePath,
				function(entry) {
					/* 读取最终存放文件夹 */
					plus.io.requestFileSystem(plus.io.PUBLIC_DOCUMENTS, function(toFile) {
						entry.moveTo(
							toFile.root,
							bookName,
							function(entry1) {
								console.log('书本储存本地成功' + entry1.fullPath);
								this.$refs.popup.open()
							},
							function(e) {
								console.log('书本存储本地失败' + JSON.stringify(e));
							}
						);
					});
				},
				function(e) {
					console.log('Resolve file URL failed: ' + e.message);
				}
			);
		},
		/* 下载书本 */
		dowmloadBook(bookName) {
			plus.io.requestFileSystem(plus.io.PUBLIC_DOCUMENTS, toFile => {
				/* 读取存放书本文件夹的所有文件 */
				var directoryReader = toFile.root.createReader();
				directoryReader.readEntries(
					entries => {
						bookName = bookName + '.txt';
						/* 判断书本是否存在 */
						let hasBook = entries.find(item => {
							return item.name === bookName;
						});
						/* 不存在则下载 */
						if (!hasBook) {
							uni.downloadFile({
								url: 'http://192.168.8.6:80/download/' + bookName,
								success: res => {
									/* 保存书本到本地 */
									this.saveBookLocal(res.tempFilePath, bookName);
									console.log(res.tempFilePath)
								},
								fail: res => {
									console.log('下载失败')
								}
							});
						} else {
							console.log('图书已在书架');
							this.$refs.popup.open()
							setTimeout(()=>{
								this.$refs.popup.close()
							},1000)
						}
					},
					function(e) {
						console.log('读取存放文件夹失败 ' + e.message);
					}
				);
			});
		}
	},
	onLoad(e){
		this.bookName=e.bookName
	},
	onReady(){
		// plus.navigator.setStatusBarStyle("dark"); //黑色
		console.log(1,this.hasBookShelf)
		plus.io.requestFileSystem(plus.io.PUBLIC_DOCUMENTS, toFile => {
			/* 读取存放书本文件夹的所有文件 */
			console.log(2,this.hasBookShelf)
			var directoryReader = toFile.root.createReader();
			directoryReader.readEntries(
				entries => {
					console.log(3,this.hasBookShelf)
					const bookName = this.bookName + '.txt';
					/* 判断书本是否存在 */
					let hasBook = entries.find(item => {
						return item.name === bookName
					});
					/* 不存在则下载 */
					if (!hasBook) {
					this.hasBookShelf=false
					}
				},
				function(e) {
					console.log('读取存放文件夹失败 ' + e.message);
				}
			);
		});
	}
};
